import React from 'react';
import './InfoCardLong.scss';
import PersonalInfo from './PersonalInfo/PersonalInfo';

export default (props) => {
  if (!props.isOn) {
    return <div></div>
  }
  const groupMembers = require('./groupMembers.js');
  const peopleToDisplay = groupMembers.default.map((member, i) => 
    <PersonalInfo 
      key={i}
      src={member.src}
      name={member.name}
      text={member.text}
      reversed={i % 2 ? true : false}
    />
  );
  return (
    <div>
      <div className="page-info-extended">
        <h2>Fáilte!</h2> 

        <p>Grupa Treblers to profesjonalny zespół tańca irlandzkiego którego nazwa pochodzi od podstawowego elementu tanecznego w stepie zwanego "treble". Wśród tancerzy Treblers są wielokrotni mistrzowie Europy – to jedyny zespół w Polsce z tak utytułowanymi artystami, prezentującymi najwyższy międzynarodowy poziom tańca irlandzkiego.</p>

        <p>Tancerze z długoletnim doświadczeniem scenicznym połączyli siły i od 2012 r. tańczą razem współtworząc choreografie. Najnowszym projektem taneczno-muzycznym jest widowisko irlandzkie „Feel the Reel” współtworzone razem z muzykami z zespołu Taorluath.</p>

        <p>W latach 2013-2017 razem z muzykami grupy Carrantuohill współtworzyli spektakl "Touch of Ireland - The Storm".</p>
       
        {peopleToDisplay}
        <p>
          W poprzednich latach tańczyli z nami również:
          Ela Wojakowska, 
          Agnieszka Bujak,
          Julia Konopka-Żołnierczuk,
          Aleksandra Sałbut,
          Milena Rogowska,
          Dorota Kucharek,
          Aleksandra Kałużny,
          Eliza Andrzejewska,
          Kateryna Derkachova,
          Karolina Lipińska,
          Natalia Aduszkiewicz,
          Aleksandra Borowska,
          Joanna Kierzkowska,
          Magdalena Niepiekło,
          Mateusz Wójcik,
          Anna Skupień,
          Anna Braćko,
          Edyta Jakubowska,
          Magdalena Lempert.
          Dziękujemy!

        </p>
      </div>
    </div>
  )
}
